.contact {
  background-color: #191919;
}
h1.hire__text {
  font-size: 60px;
}
p.hire__text {
  font-size: 18px;
  line-height: 30px;
  color: rgba(29, 29, 36, 0.75);
  font-weight: 400;
}
input.contact {
  display: block;
  width: 100%;
  padding: 0 20px;
  border: 2px solid rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  height: 50px;
  line-height: 46px;
  margin: 20px 0px;
  outline: none;
  color: #fff;
  font-size: 15px;
  letter-spacing: 0.1px;
  background: none;
  border: 2px solid hsla(0, 0%, 100%, 0.2);
}

textarea#message {
  height: 120px;
  background: none;
  padding: 0 20px;
  width: 100%;
  line-height: 46px;
  color: #fff;
  font-size: 15px;
  letter-spacing: 0.1px;
  border: 2px solid hsla(0, 0%, 100%, 0.2);
}
button.btn.contact.pointer {
  margin: 20px 0px;
}

@media only screen and (max-width: 768px) {
  button.btn {
    font-size: 15px;
    height: 40px;
    width: 120px;
  }
}
@media only screen and (max-width: 425px) {
  .contact__meta {
    text-align: center;
  }
  h1.hire__text {
    font-size: 48px;
  }
  .input__box {
    width: 60%;
    margin: 0 auto;
  }
  input.contact {
    height: 35px;
  }
}
